export function getSelectedVersions(): string[] {
    const old_sel = document.getElementById("old_version") as HTMLSelectElement;
    const new_sel = document.getElementById("new_version") as HTMLSelectElement;
    return [old_sel.value, new_sel.value];
}

export function setupVersionSelector(parent_id: string, version_list: string[], onchange_cb: () => void): void {
    function createVersionSelect(id: string) {
        const ver_select = document.createElement("select");
        ver_select.id = id;
        for (const ver of version_list) {
            const opt = document.createElement("option");
            opt.value = ver;
            opt.innerText = "Version " + ver;
            ver_select.appendChild(opt);
        }
        return ver_select;
    }

    const old_select = createVersionSelect("old_version");
    const new_select = createVersionSelect("new_version");
    new_select.selectedIndex = new_select.childElementCount - 1;
    if (old_select.childNodes.length > 1) {
        old_select.selectedIndex = old_select.childElementCount - 2;
    } else {
        old_select.selectedIndex = old_select.childElementCount - 1;
    }

    const select_parent = document.getElementById(parent_id);
    if (!select_parent) {
        throw new Error("boom");
    }
    const new_hdr = document.createElement("h3");
    new_hdr.innerText = "Show version";
    const old_hdr = document.createElement("h3");
    old_hdr.innerText = "Relative to version";
    select_parent.appendChild(new_hdr);
    select_parent.appendChild(new_select);
    select_parent.appendChild(old_hdr);
    select_parent.appendChild(old_select);
    old_select.onchange = onchange_cb;
    new_select.onchange = onchange_cb;
}
