import { prettyPlatformName, prettyMeasurementName } from "./helpers";
import { DataRecord } from "./schemas";
import { DataTree, Stats } from "./types";
import { getSelectedVersions } from "./ui";

// Gather statistics data for selected versions
export function statsForVersions(root: DataTree, prev_version: string, cur_version: string): Stats {
    const stats: Stats = {
        platformNames: root.map(elem => elem.name),
        data: {},
    };

    for (const { name: platform_name, measurements } of root) {
        // Not all measurements are suited for inclusion in the stats table
        for (const measurement of measurements) {
            if (!["ttfi", "ttvv", "tthpc", "ttli"].includes(measurement.name)) {
                continue;
            }
            if (!(measurement.name in stats.data)) {
                stats.data[measurement.name] = { unit: measurement.unit, data: {} };
            }
            const entry = stats.data[measurement.name].data;

            const prev_recs = measurement.versions.find(v => v.name === prev_version)?.records ?? [];
            const cur_recs = measurement.versions.find(v => v.name === cur_version)?.records ?? [];

            // Pick out the property from each record that we want to present
            function representativeValues(records: DataRecord[]) {
                // We assume that the minimum value represents the sunshine scenario.
                // The other values by definition include more noise
                return records.map(r => r.min).filter(Number.isFinite);
            }

            const rep_cur = representativeValues(cur_recs);
            const rep_prev = representativeValues(prev_recs);

            const cur_has_missing = rep_cur.length !== cur_recs.length;
            const prev_has_missing = rep_prev.length !== prev_recs.length;

            entry[platform_name] = {
                lowest: {
                    new: Math.min(...representativeValues(cur_recs)),
                    old: Math.min(...representativeValues(prev_recs)),
                },
                highest: {
                    new: Math.max(...representativeValues(cur_recs)),
                    old: Math.max(...representativeValues(prev_recs)),
                },
                cumulative: {
                    new: cur_has_missing ? Number.NaN : representativeValues(cur_recs).reduce((acc, v) => acc + v, 0),
                    old: prev_has_missing ? Number.NaN : representativeValues(prev_recs).reduce((acc, v) => acc + v, 0),
                },
            };
        }
    }

    return stats;
}

export function printStatsTable(element_id: string, stats: Stats): void {
    console.log("Rendering stats table");
    // nuke the existing table
    const old_table = document.getElementById(element_id);
    if (!old_table) {
        throw new Error("Boom");
    }
    const table = old_table.cloneNode(false);
    if (!old_table.parentNode) {
        throw new Error("Boom");
    }
    old_table.parentNode.replaceChild(table, old_table);

    // fill in the platform header row
    const thead = document.createElement("thead");
    table.appendChild(thead);
    thead.appendChild(document.createElement("th"));
    stats.platformNames.forEach(p => {
        const th = document.createElement("th");
        th.textContent = prettyPlatformName(p);
        thead.appendChild(th);
    });

    // For each measurement
    for (const [measurement_name, measurement_data] of Object.entries(stats.data)) {
        // First generate a header
        const hdr_tr = document.createElement("tr");
        const hdr_th = document.createElement("th");
        hdr_th.textContent = `${prettyMeasurementName(measurement_name)} (sum)`;
        hdr_th.colSpan = stats.platformNames.length + 1;
        hdr_th.className = "top";
        hdr_tr.appendChild(hdr_th);
        table.appendChild(hdr_tr);

        function createRow(label: string) {
            const row = document.createElement("tr");
            const th = document.createElement("th");
            th.textContent = label;
            th.className = "left";
            row.appendChild(th);
            table.appendChild(row);

            return row;
        }

        // Then, for each statistic, fill in the data rows
        const stringify = (val: number, unit: string) =>
            Number.isFinite(val) ? `${val.toFixed(2)}${unit}` : "<Missing Data>";

        const row_new = createRow(getSelectedVersions()[1]);
        const row_old = createRow(getSelectedVersions()[0]);
        const row_rel = createRow(`Change (${getSelectedVersions()[0]} to ${getSelectedVersions()[1]})`);

        const unit = measurement_data.unit;
        // For each platform, add a data column
        for (const data of Object.values(measurement_data.data)) {
            const append_cell = (row: HTMLTableRowElement, value: string, range?: string) => {
                const cell_new = document.createElement("td");
                cell_new.textContent = value + " " + (range ?? "");
                row.appendChild(cell_new);
            };

            append_cell(
                row_new,
                stringify(data.cumulative.new, unit),
                `(${stringify(data.lowest.new, unit)} - ${stringify(data.highest.new, unit)})`
            );
            append_cell(
                row_old,
                stringify(data.cumulative.old, unit),
                `(${stringify(data.lowest.old, unit)} - ${stringify(data.highest.old, unit)})`
            );
            append_cell(row_rel, stringify((data.cumulative.new / data.cumulative.old) * 100, "%"));
        }
    }
}
