import { Diagnostic } from "./schemas";
import { MissingEntryDesc } from "./types";
import { getSelectedVersions } from "./ui";

export function printMissingTable(element_id: string, missing_info: MissingEntryDesc[]) {
    const old_container = document.getElementById(element_id);
    if (!old_container) {
        throw new Error("Boom");
    }
    const container = old_container.cloneNode(false);
    if (!old_container.parentNode) {
        throw new Error("Boom");
    }
    old_container.parentNode.replaceChild(container, old_container);

    const selected_versions = getSelectedVersions();
    missing_info = missing_info.filter(record => selected_versions.includes(record.version));

    if (missing_info.length === 0) {
        (container as HTMLElement).style.display = "none";
        return;
    }
    (container as HTMLElement).style.display = "block";

    const header = document.createElement("h2");
    header.textContent = "Missing Records";
    container.appendChild(header);

    const radio_container = document.createElement("formset");
    const enable_summary = document.createElement("input");
    const enable_full = document.createElement("input");
    container.appendChild(radio_container);
    enable_summary.type = "radio";
    enable_summary.id = "enable_summary";
    enable_summary.name = "missing_selector";
    enable_summary.checked = true;
    enable_full.type = "radio";
    enable_full.name = "missing_selector";
    enable_full.id = "enable_full";

    const enable_summary_label = document.createElement("label");
    enable_summary_label.htmlFor = "enable_summary";
    enable_summary_label.textContent = "Show Summary";
    radio_container.appendChild(enable_summary);
    radio_container.append(enable_summary_label);
    const enable_full_label = document.createElement("label");
    enable_full_label.htmlFor = "enable_full";
    enable_full_label.textContent = "Show All Missing Entries";
    radio_container.appendChild(enable_full);
    radio_container.appendChild(enable_full_label);

    const content_container = document.createElement("div");
    container.appendChild(content_container);
    const full_container = document.createElement("pre");
    full_container.className = "missing_full";
    content_container.append(full_container);

    const missing_strings = missing_info
        .map(entry => `${entry.version} - ${entry.measurement} - ${entry.archive} - ${entry.study}`)
        .sort();
    full_container.textContent = missing_strings.join("\n");

    const summary_container = document.createElement("div");
    summary_container.className = "missing_summary";
    const missing_list = document.createElement("ul");
    content_container.appendChild(summary_container);
    summary_container.appendChild(missing_list);

    const countMissing = (propname: keyof MissingEntryDesc) =>
        missing_info.reduce<Record<string, number>>((acc, val) => {
            acc[val[propname]] = (acc[val[propname]] ?? 0) + 1;
            return acc;
        }, {});

    const missing_by_version = countMissing("version");
    const missing_by_measurement = countMissing("measurement");
    const missing_by_archive = countMissing("archive");
    const missing_by_study = countMissing("study");

    const append = (prefix: string, list: Record<string, number>) => {
        for (const [key, value] of Object.entries(list)) {
            const item = document.createElement("li");
            item.textContent = `${prefix}: ${key} missing ${value} records`;
            missing_list.appendChild(item);
        }
    };

    append("Version", missing_by_version);
    append("Measurement", missing_by_measurement);
    append("Archive", missing_by_archive);
    append("Study", missing_by_study);

    // Finally, wire up the radio buttons to toggle between summary and full view
    enable_summary.addEventListener("change", () => {
        summary_container.style.display = "block";
        full_container.style.display = "none";
    });
    enable_full.addEventListener("change", () => {
        full_container.style.display = "block";
        summary_container.style.display = "none";
    });
    summary_container.style.display = "block";
    full_container.style.display = "none";
}

export function renderDiagnostics(diags: Diagnostic[]) {
    if (diags.length === 0) {
        return;
    }

    const div = document.createElement("div");
    div.classList.add("diagnostics");
    const errors = diags.filter(d => d.severity === "error");
    const others = diags.filter(d => d.severity !== "error");
    const list = errors.concat(others);

    list.forEach(msg => {
        const line = document.createElement("div");
        line.classList.add(msg.severity);
        const severity = document.createElement("span");
        severity.textContent = msg.severity + ": ";
        line.appendChild(severity);
        const text = document.createElement("span");
        text.textContent = msg.message + ": ";
        line.appendChild(text);
        const study = document.createElement("span");
        study.textContent = msg.study ? "(" + msg.study + ")" : "";
        line.appendChild(study);
        div.appendChild(line);
    });

    const body = document.getElementsByTagName("body")[0];
    body.prepend(div);

    const info = document.createElement("a");
    info.textContent = "(See diagnostics.json for further details)";
    info.href = "diagnostics.json";
    body.prepend(info);

    const header = document.createElement("h2");
    header.textContent = "Diagnostic Messages";
    body.prepend(header);
}
