import {
    DataRecord,
    diagnostics_schema,
    Goals,
    goals_schema,
    Manifest,
    manifest_schema,
    raw_records_schema,
} from "./schemas";
import { isKnownMeasurement, orderMeasurements, prettyName, unique } from "./helpers";
import { renderCharts } from "./graphs";
import { Stats, DataTree, MissingEntryDesc } from "./types";
import { printMissingTable, renderDiagnostics } from "./diagnostics";
import { printStatsTable, statsForVersions } from "./stats";
import { getSelectedVersions, setupVersionSelector } from "./ui";

function statsForSelectedVersions(root: DataTree): Stats {
    const vers = getSelectedVersions();
    console.log("Selected versions", vers);
    return statsForVersions(root, vers[0], vers[1]);
}

// Synthesize fake records for every one that are missing in the source data
// This ensures that we always have a consistent number of records, no matter which version,
// archive, study or measurement we filter by
function addDummyRecords(
    records: DataRecord[],
    dummy_base: Omit<DataRecord, "version" | "name" | "study" | "archive">,
    missing_info?: MissingEntryDesc[]
) {
    // Gather lists of unique keys.
    // The expectation is that for every combination of these, one record should exist
    const versions = unique(records.map(r => r.version));
    const archives = unique(records.map(r => r.archive));
    const studies = unique(records.map(r => r.study));

    // Names is synthezised as a combination of archive and study
    const names = unique(records.map(r => prettyName(r)));

    const index: Record<string, Record<string, DataRecord>> = {};
    records.forEach(r => {
        const v = r.version;
        const n = prettyName(r);

        if (!(v in index)) {
            index[v] = {};
        }
        if (n in index[v]) {
            throw new Error(
                "Oops, multiple records found with the same keys: " +
                    JSON.stringify(r) +
                    " and " +
                    JSON.stringify(index[v][n])
            );
        }

        index[v][n] = r;
    });

    // look up each combination of keys. If record is missing, add a dummy
    for (const version of versions) {
        for (const archive of archives) {
            for (const study of studies) {
                if (!(version in index)) {
                    index[version] = {};
                }
                const n = prettyName({ study, archive });
                if (!(n in index[version])) {
                    console.log("Missing record", n, "for version", version, ". Inserting dummy value");
                    index[version][n] = Object.assign({ version, archive, study }, dummy_base);
                    if (Number.isNaN(dummy_base.max)) {
                        missing_info?.push({ version, archive, study, measurement: dummy_base.measurement });
                    }
                }
            }
        }
    }

    // serialize back to an array, sorted by name
    const result = versions
        .reduce<DataRecord[]>((acc, v) => {
            return names.reduce((acc, n) => acc.concat([index[v][n]]), acc);
        }, [])
        .sort((lhs, rhs) => prettyName(lhs).localeCompare(prettyName(rhs)));
    if (records.length !== result.length) {
        console.log("After inserting dummy records", result);
    }
    return result;
}

// Fetch manifest.json, describing which platforms and versions should be displayed
async function fetchManifest() {
    return manifest_schema.parse(await (await fetch("manifest.json")).json());
}

// Fetch all the data files and return all retrieved records as a single array, ordered by name
async function fetchRecords(manifest: Manifest): Promise<DataRecord[]> {
    const datasources: { platform: string; version: string; filename: string }[] = [];
    for (const platform of manifest.platforms) {
        for (const version of manifest.versions) {
            const name = `stats-${platform}-${version}.json`;
            datasources.push({
                platform: platform,
                version: version,
                filename: name,
            });
            console.log("Requiring data file", name);
        }
    }

    const data_with_source_info = await Promise.all(
        datasources.map(async source => {
            return {
                data: raw_records_schema.parse(await (await fetch(source.filename)).json()),
                source,
            };
        })
    );

    const sources = data_with_source_info.flatMap(({ data, source }) =>
        data.map(entry => ({
            ...entry,
            version: source.version,
            platform: source.platform,
        }))
    );

    return sources.sort((lhs, rhs) => prettyName(lhs).localeCompare(prettyName(rhs)));
}

// Organize records in a tree structure
// ordered by platform, measurement and version from root to leaves.
// each leaf node contains the relevant subset of records
// records arrays are ordered by name
// dummy objects are inserted where entries are missing
// versions and platforms arrays are in manifest order
// measurements arrays are ordered alphabetically
function buildDataTree(manifest: Manifest, records: DataRecord[], missing_info?: MissingEntryDesc[]): DataTree {
    console.log("Building data structure");
    const measurement_names = unique(records.map(r => r.measurement))
        .filter(isKnownMeasurement)
        .sort(orderMeasurements);

    const root = manifest.platforms.map(platform => {
        console.log("platform: " + platform);
        const records_for_platform = records.filter(record => {
            return record.platform === platform;
        });

        const measurement_nodes = measurement_names.map(measurement_name => {
            console.log("- measurement: " + measurement_name);
            let records_for_measurement = records_for_platform.filter(record => {
                return record.measurement === measurement_name;
            });

            const versions = unique(records_for_measurement.map(r => r.version)).sort(
                (lhs, rhs) =>
                    manifest.versions.findIndex(item => item === lhs) -
                    manifest.versions.findIndex(item => item === rhs)
            );

            const treat_missing_as_zero = measurement_name === "tti" || measurement_name === "slow_event_time";
            let unit = records_for_measurement.length !== 0 ? records_for_measurement[0].unit : "";
            // normalize the data. Within a measurement all combinations of version
            // and name should have a record, all time-based measurements should be in seconds, and all byte-based in MiB
            records_for_measurement = addDummyRecords(
                records_for_measurement,
                {
                    measurement: measurement_name,
                    platform: platform,
                    min: treat_missing_as_zero ? 0 : Number.NaN,
                    max: treat_missing_as_zero ? 0 : Number.NaN,
                    median: treat_missing_as_zero ? 0 : Number.NaN,
                    mean: treat_missing_as_zero ? 0 : Number.NaN,
                    unit,
                },
                missing_info
            );

            if (unit === "ms") {
                unit = "s";
                records_for_measurement = records_for_measurement.map(r => {
                    r.min = r.min ? r.min / 1000 : r.min;
                    r.max = r.max ? r.max / 1000 : r.max;
                    r.median = r.median ? r.median / 1000 : r.median;
                    r.mean = r.mean ? r.mean / 1000 : r.mean;
                    return r;
                });
            } else if (unit === "seconds") {
                unit = "s";
            } else if (unit === "bytes") {
                unit = "MiB";
                const div = 1024 * 1024;
                records_for_measurement = records_for_measurement.map(r => {
                    r.min = r.min ? r.min / div : r.min;
                    r.max = r.max ? r.max / div : r.max;
                    r.median = r.median ? r.median / div : r.median;
                    r.mean = r.mean ? r.mean / div : r.mean;
                    return r;
                });
            }

            // Add child nodes for versions
            const version_nodes = versions.map(version => {
                console.log("  - version: " + version);
                const version_data = {
                    name: version,
                    records: records_for_measurement.filter(record => record.version === version),
                };

                return version_data;
            });
            return {
                name: measurement_name,
                versions: version_nodes,
                unit,
            };
        });
        return {
            name: platform,
            measurements: measurement_nodes,
        };
    });

    console.log("Data structure complete");
    return root;
}

// Allow the Window object to be extended with our main data structures, for debugging
const window_debug = window as unknown as {
    benchmark_data: {
        root: unknown;
        manifest: unknown;
        source: unknown;
        diagnostics?: unknown;
    };
};

export async function loadForBatchMode() {
    let goals: Goals = {};
    try {
        goals = goals_schema.parse(await (await fetch("goals.json")).json());
    } catch (error: unknown) {
        console.log("Failed to load goals: ", error);
    }
    const manifest = await fetchManifest();
    const records = await fetchRecords(manifest);
    const missing_info: MissingEntryDesc[] = [];
    const root = buildDataTree(manifest, records, missing_info);

    setupVersionSelector("version_select", manifest.versions, () => {
        printStatsTable("stats_table", statsForSelectedVersions(root));
        printMissingTable("missing_table", missing_info);
    });
    printStatsTable("stats_table", statsForSelectedVersions(root));
    printMissingTable("missing_table", missing_info);
    // for debug
    window_debug.benchmark_data = { root, manifest, source: records };

    renderCharts(root, manifest.versions, goals);
}

export async function loadForSingleMode() {
    const response = await fetch("stats.json");
    const diags = diagnostics_schema.parse(await (await fetch("diagnostics.json")).json());

    let goals: Goals = {};
    try {
        goals = goals_schema.parse(await (await fetch("goals.json")).json());
    } catch (error: unknown) {
        console.log("Failed to load goals: ", error);
    }

    const records = raw_records_schema
        .parse(await response.json())
        .map(r => ({ ...r, name: r.study, version: r.archive, platform: "" }));
    records.sort((lhs, rhs) => lhs.name.localeCompare(rhs.name));

    const manifest: Manifest = {
        platforms: [""],
        versions: unique(records.map(r => r.archive)).sort(),
    };

    const root = buildDataTree(manifest, records);

    // for debug
    window_debug.benchmark_data = { root, manifest, source: records, diagnostics: diags };

    renderCharts(root, manifest.versions, goals);

    renderDiagnostics(diags);
}
