import { z } from "zod";

export const manifest_schema = z.object({
    platforms: z.array(z.string()),
    versions: z.array(z.string()),
    input: z.string().optional(),
    config: z.array(z.object({ url: z.string(), version: z.string(), platform: z.string() })).optional(),
});

export type Manifest = z.infer<typeof manifest_schema>;

export const raw_records_schema = z
    .object({
        measurement: z.string(),
        platform: z.string(),
        version: z.string(),
        archive: z.string(),
        study: z.string(),
        unit: z.string(),
        median: z.number(),
        mean: z.number(),
        min: z.number(),
        max: z.number(),
    })
    .array();

export type DataRecord = {
    archive: string;
    study: string;
    measurement: string;
    platform: string;
    version: string;
    unit: string;
    median: number;
    mean: number;
    min: number;
    max: number;
};

export const diagnostics_schema = z
    .object({
        severity: z.string(),
        message: z.string(),
        study: z.string().optional(),
    })
    .array();

export type Diagnostic = z.infer<typeof diagnostics_schema.element>;

export const goals_schema = z.record(
    z.string(),
    z
        .object({
            label: z.string(),
            value: z.number(),
        })
        .array()
);

export type Goals = z.infer<typeof goals_schema>;
