import Chart from "chart.js/auto";
import annotationPlugin from "chartjs-plugin-annotation";
import { BarWithErrorBarsController, BarWithErrorBar } from "chartjs-chart-error-bars";
import { prettyPlatformName, prettyMeasurementName, measurementNameDescription, prettyName } from "./helpers";
import { Goals } from "./schemas";
import { Measurement, DataTree } from "./types";

Chart.register(annotationPlugin, BarWithErrorBarsController, BarWithErrorBar);
Chart.defaults.animation = false;

function createGoalsAnnotations(measurement: Measurement, goals: Goals) {
    const result: Record<string, unknown> = {};
    if (measurement.name in goals) {
        for (const x of goals[measurement.name]) {
            result["hline:" + x.label] = {
                type: "line",
                yMin: x.value,
                yMax: x.value,
                borderColor: "black",
                borderWidth: 1,
                label: {
                    backgroundColor: "red",
                    content: x.label,
                    enabled: true,
                },
            };
        }
    }
    console.log("result:");
    console.dir(result);
    return result;
}

export function renderChart(
    element: HTMLElement,
    measurement: Measurement,
    version_names: string[],
    goals: Goals
): void {
    const color_lookup = new Map<string, number>();
    version_names.forEach((v, i) => {
        color_lookup.set(v, i);
    });

    const colors = [
        "rgb(206, 252, 141)",
        "rgb(163, 198, 255)",
        "rgb(175, 117, 234)",
        "rgb(234, 107, 137)",
        "rgb(242, 116, 240)",
        "rgb(120, 113, 247)",
        "rgb(252, 184, 189)",
        "rgb(162, 232, 239)",
    ];

    const data = {
        // Our data is normalized so the record names are the same for every version
        labels: measurement.versions[0].records.map(record => prettyName(record)),
        datasets: measurement.versions.map(version => {
            const color = colors[color_lookup.get(version.name) ?? 0];
            const dataset = {
                label: version.name,
                borderColor: color,
                backgroundColor: color,
                data: version.records.map(record => ({
                    y: record.min,
                    yMin: record.min,
                    yMax: record.max,
                })),
            };
            return dataset;
        }),
    };

    const container = document.createElement("div");
    container.style.position = "relative";
    container.style.width = "100%";
    const canvas = document.createElement("canvas");
    element.appendChild(container);
    container.appendChild(canvas);
    const ctx = canvas.getContext("2d");
    if (!ctx) {
        throw new Error("Could not get 2D context");
    }
    new Chart(ctx, {
        type: BarWithErrorBarsController.id,
        data: data,
        options: {
            plugins: {
                legend: {
                    display: true,
                    position: "top",
                },
                annotation: {
                    common: {
                        drawTime: "afterDatasetsDraw",
                    },
                    // eslint-disable-next-line @typescript-eslint/no-unsafe-assignment, @typescript-eslint/no-explicit-any
                    annotations: createGoalsAnnotations(measurement, goals) as any,
                },
            },
            scales: {
                y: {
                    ticks: {
                        callback: function (tickValue) {
                            return Number(tickValue).toFixed(3);
                        },
                    },
                },
                x: {
                    ticks: {
                        autoSkip: false,
                        maxRotation: 60,
                        minRotation: 60,
                    },
                },
            },
        },
    });
}

export function renderCharts(root: DataTree, versions: string[], goals: Goals): void {
    console.log("Rendering charts");
    const body = document.getElementsByTagName("body")[0];

    root.forEach(platform => {
        const platform_div = document.createElement("div");
        const platform_header = document.createElement("h1");
        platform_header.innerText = prettyPlatformName(platform.name);
        platform_header.id = "hdr-" + platform.name;
        platform_header.onclick = () => {
            location.href = "#" + platform_header.id;
        };
        platform_div.appendChild(platform_header);

        platform.measurements.forEach(measurement => {
            const measurement_header = document.createElement("h2");
            const name = prettyMeasurementName(measurement.name) + " (" + measurement.unit + ")";
            const desc = measurementNameDescription(measurement.name);
            measurement_header.innerText = name;
            measurement_header.title = desc;
            measurement_header.id = "hdr-" + platform.name + "-" + measurement.name;
            measurement_header.onclick = () => {
                location.href = "#" + measurement_header.id;
            };
            platform_div.appendChild(measurement_header);
            renderChart(platform_div, measurement, versions, goals);
        });

        body.appendChild(platform_div);
    });
}
