import { DataRecord } from "./schemas";

// Descriptive metadata for each measurement
export const measurements_table = [
    {
        key: "ttfi",
        name: "Time to First Image Shown",
        desc: "Time from user started a CAM until the first image has been rendered",
    },
    {
        key: "ttvv",
        name: "Time to All Visible Viewports Shown",
        desc: "Time from user started a CAM until all non-empty viewports on the current tab have rendered an image",
    },
    {
        key: "tthpc",
        name: "Time to Hanging Protocols Complete",
        desc: "Time until the hanging protocol pipelines have been run and layout and viewports have been determined",
    },
    {
        key: "tthc",
        name: "Time to Hanging Complete",
        desc: "Time until the hanging protocols have received all scheduled DicomObjects. Equivalent to loading bar finished.",
    },
    {
        key: "ttli",
        name: "Time to Last Image Fetched",
        desc: "Time until all images in the initial primary and prior studies have been fetched",
    },
    {
        key: "tti",
        name: "Time to interactive",
        desc: "Time until the last 500+ ms non-responsive period has passed",
    },
    { key: "tspd", name: "Transfer Rate", desc: "Transfer Rate, MB/s" },
    {
        key: "slow_event_time",
        name: "Non-responsive time",
        desc: "Cumulative time spent processing events which each took more than 500ms",
    },
    {
        key: "cpu_time",
        name: "CPU time during load",
        desc: "CPU time used while loading data",
    },
    {
        key: "peak_mem",
        name: "Peak resident memory usage",
        desc: "Peak amount of memory mapped into RAM",
    },
    {
        key: "peak_mem_to_pixel",
        name: "Memory usage efficiency",
        desc: "Ratio: Peak memory to uncompressed pixel size",
    },
];

export function prettyPlatformName(platform: string) {
    switch (platform) {
        case "win32":
            return "Windows";
        case "linux":
            return "Linux";
        default:
            return platform;
    }
}

export function isKnownMeasurement(measurement: string) {
    return measurements_table.map(x => x.key).includes(measurement);
}

export function orderMeasurements(lhs: string, rhs: string) {
    const lhs_idx = measurements_table.findIndex(item => item.key === lhs);
    const rhs_idx = measurements_table.findIndex(item => item.key === rhs);

    if (lhs_idx === -1 && rhs_idx === -1) {
        return lhs.localeCompare(rhs);
    }
    return lhs_idx - rhs_idx;
}

export function prettyMeasurementName(measurement: string) {
    const m = measurements_table.find(item => item.key === measurement);
    return m ? m.name : measurement;
}
export function measurementNameDescription(measurement: string) {
    const m = measurements_table.find(item => item.key === measurement);
    return m ? m.desc : "";
}

export function prettyName(r: Pick<DataRecord, "archive" | "study">) {
    return `(${r.archive}) ${r.study}`;
}

export function unique<T>(entries: T[]) {
    return [
        ...entries.reduce((acc, val) => {
            acc.add(val);
            return acc;
        }, new Set<T>()),
    ];
}
